import { useAppDispatch } from "common/hooks";
import type { Todolist } from "../api/todolistsApi.types";
import { todolistsApi } from "../api/todolistsApi";
import { setAppStatus, type RequestStatus } from "app/appSlice";
import { ResultCode } from "../lib/enums/enums";
import { handleHttpErrors } from "common/utils/handleHttpErrors";
import { handleAppErrors } from "common/utils/handleAppErrors";
import { fetchTasksTC } from "./tasksSlice";
import { createSlice } from "@reduxjs/toolkit";
import type { AppDispatch } from "app/store";

export type FilterValuesType = "all" | "active" | "completed";

export type DomainTodolist = Todolist & {
  filter: FilterValuesType;
  entityStatus: RequestStatus;
};

export const todolistsSlice = createSlice({
  name: "todolists",
  initialState: [] as DomainTodolist[],
  reducers: (create) => ({
    removeTodolist: create.reducer<{ id: string }>((state, action) => {
      const index = state.findIndex((t) => t.id === action.payload.id);
      if (index !== -1) state.splice(index, 1);
    }),
    addTodolist: create.reducer<{ todolist: Todolist }>((state, action) => {
      const newTodolist: DomainTodolist = {
        ...action.payload.todolist,
        filter: "all",
        entityStatus: "idle",
      };
      state.unshift(newTodolist);
    }),
    changeTodolistTitle: create.reducer<{ id: string; title: string }>(
      (state, action) => {
        const index = state.findIndex((t) => t.id === action.payload.id);
        if (index !== -1) state[index].title = action.payload.title;
      }
    ),
    changeTodolistFilter: create.reducer<{
      id: string;
      filter: FilterValuesType;
    }>((state, action) => {
      const index = state.findIndex((t) => t.id === action.payload.id);
      if (index !== -1) state[index].filter = action.payload.filter;
    }),
    changeTodolistStatus: create.reducer<{
      id: string;
      entityStatus: RequestStatus;
    }>((state, action) => {
      const todolist = state.find((t) => t.id === action.payload.id);
      if (todolist) todolist.entityStatus = action.payload.entityStatus;
    }),
    setTodolists: create.reducer<{ todolists: Todolist[] }>((state, action) => {
      action.payload.todolists.forEach((t) => {
        state.push({ ...t, filter: "all", entityStatus: "idle" });
      });
    }),
    clearTodolistsData: create.reducer(() => {
      return [];
    }),
  }),
  selectors: {
    selectTodolists: (state) => state,
  },
});

export const todolistsReducer = todolistsSlice.reducer;
export const {
  removeTodolist,
  addTodolist,
  changeTodolistFilter,
  changeTodolistStatus,
  changeTodolistTitle,
  clearTodolistsData,
  setTodolists,
} = todolistsSlice.actions;
export const { selectTodolists } = todolistsSlice.selectors;

// Thunks
export const fetchTodolistsTC = () => (dispatch: AppDispatch) => {
  dispatch(setAppStatus({ status: "loading" }));
  todolistsApi
    .getTodolists()
    .then((res) => {
      dispatch(setAppStatus({ status: "succeeded" }));
      dispatch(setTodolists({ todolists: res.data }));
      return res.data;
    })
    .then((todos) => {
      todos.forEach((tl) => {
        dispatch(fetchTasksTC(tl.id));
      });
    })
    .catch((err) => {
      handleHttpErrors(err, dispatch);
    });
};

export const addTodolistTC = (title: string) => (dispatch: AppDispatch) => {
  dispatch(setAppStatus({ status: "loading" }));
  todolistsApi
    .createTodolist(title)
    .then((res) => {
      if (res.data.resultCode === ResultCode.Success) {
        dispatch(setAppStatus({ status: "succeeded" }));
        dispatch(addTodolist({ todolist: res.data.data.item }));
      } else {
        handleAppErrors(dispatch, res.data);
      }
    })
    .catch((err) => {
      handleHttpErrors(err, dispatch);
    });
};

export const removeTodolistTC = (id: string) => (dispatch: AppDispatch) => {
  dispatch(setAppStatus({ status: "loading" }));
  dispatch(changeTodolistStatus({ entityStatus: "loading", id }));
  todolistsApi
    .deleteTodolist(id)
    .then((res) => {
      if (res.data.resultCode === ResultCode.Success) {
        dispatch(setAppStatus({ status: "succeeded" }));
        dispatch(removeTodolist({ id }));
      } else {
        handleAppErrors(dispatch, res.data);
        dispatch(changeTodolistStatus({ entityStatus: "succeeded", id }));
      }
    })
    .catch((err) => {
      handleHttpErrors(err, dispatch);
      dispatch(changeTodolistStatus({ entityStatus: "succeeded", id }));
    });
};

export const updateTodolistTitleTC =
  (payload: { id: string; title: string }) => (dispatch: AppDispatch) => {
    dispatch(setAppStatus({ status: "loading" }));
    todolistsApi
      .updateTodolistTitle(payload)
      .then((res) => {
        if (res.data.resultCode === ResultCode.Success) {
          dispatch(setAppStatus({ status: "succeeded" }));
          dispatch(changeTodolistTitle(payload));
        } else {
          handleAppErrors(dispatch, res.data);
        }
      })
      .catch((err) => {
        handleHttpErrors(err, dispatch);
      });
  };
