import type { LoginInputs } from "../ui/Login/Login";
import { authApi } from "../api/authApi";
import { ResultCode } from "features/todolists/lib/enums/enums";
import { handleHttpErrors } from "common/utils/handleHttpErrors";
import { handleAppErrors } from "common/utils/handleAppErrors";
import { createSlice } from "@reduxjs/toolkit";
import { setAppStatus } from "app/appSlice";
import { clearTodolistsData } from "features/todolists/model/todolistsSlice";
import { clearData } from "features/todolists/model/tasksSlice";
import type { AppDispatch } from "app/store";

export const authSlice = createSlice({
  name: "auth",
  initialState: {
    isLoggedIn: false,
    isInitialized: false,
  },
  reducers: (create) => {
    return {
      setIsLoggedIn: create.reducer<{ isLoggedIn: boolean }>(
        (state, action) => {
          state.isLoggedIn = action.payload.isLoggedIn;
        }
      ),
      setIsInitialized: create.reducer<{ isInitialized: boolean }>(
        (state, action) => {
          state.isInitialized = action.payload.isInitialized;
        }
      ),
    };
  },
  selectors: {
    selectIsLoggedIn: (state) => state.isLoggedIn,
    selectIsInitialized: (state) => state.isInitialized,
  },
});

export const authReducer = authSlice.reducer;
export const { setIsLoggedIn, setIsInitialized } = authSlice.actions;
export const { selectIsLoggedIn, selectIsInitialized } = authSlice.selectors;

//THUNKS

export const loginTC = (data: LoginInputs) => (dispatch: AppDispatch) => {
  dispatch(setAppStatus({ status: "loading" }));
  authApi
    .login(data)
    .then((res) => {
      if (res.data.resultCode === ResultCode.Success) {
        dispatch(setAppStatus({ status: "succeeded" }));
        dispatch(setIsLoggedIn({ isLoggedIn: true }));
        localStorage.setItem("sn-token", res.data.data.token);
      } else {
        handleAppErrors(dispatch, res.data);
      }
    })
    .catch((e) => {
      handleHttpErrors(e, dispatch);
    });
};

export const logoutTC = () => (dispatch: AppDispatch) => {
  dispatch(setAppStatus({ status: "loading" }));
  authApi
    .logout()
    .then((res) => {
      if (res.data.resultCode === ResultCode.Success) {
        dispatch(setAppStatus({ status: "succeeded" }));
        dispatch(setIsLoggedIn({ isLoggedIn: false }));

        dispatch(clearTodolistsData());
        dispatch(clearData());
        localStorage.removeItem("sn-token");
      } else {
        handleAppErrors(dispatch, res.data);
      }
    })
    .catch((e) => {
      handleHttpErrors(e, dispatch);
    });
};

export const meTC = () => (dispatch: AppDispatch) => {
  dispatch(setAppStatus({ status: "loading" }));
  authApi
    .me()
    .then((res) => {
      if (res.data.resultCode === ResultCode.Success) {
        dispatch(setAppStatus({ status: "loading" }));
        dispatch(setIsLoggedIn({ isLoggedIn: true }));
      } else {
        handleAppErrors(dispatch, res.data);
      }
    })
    .catch((e) => {
      handleHttpErrors(e, dispatch);
    })
    .finally(() => {
      dispatch(setIsInitialized({ isInitialized: true }));
    });
};
