import type { AppDispatch, RootState } from "app/store";
import { taskApi } from "../api/tasksApi";
import type { DomainTask, UpdateTaskDomainModel } from "../api/tasksApi.types";
import { ResultCode } from "../lib/enums/enums";
import { setAppStatus } from "app/appSlice";
import { handleHttpErrors } from "common/utils/handleHttpErrors";
import { handleAppErrors } from "common/utils/handleAppErrors";
import { createSlice } from "@reduxjs/toolkit";
import { addTodolist, removeTodolist } from "./todolistsSlice";

export type TasksStateType = {
  [key: string]: DomainTask[];
};

export const tasksSlice = createSlice({
  name: "tasks",
  initialState: {} as TasksStateType,
  reducers: (create) => ({
    setTasks: create.reducer<{ todolistId: string; tasks: DomainTask[] }>(
      (state, action) => {
        state[action.payload.todolistId] = action.payload.tasks;
      }
    ),
    removeTask: create.reducer<{ taskId: string; todolistId: string }>(
      (state, action) => {
        const tasksForCurrentTodolist = state[action.payload.todolistId];
        const index = tasksForCurrentTodolist.findIndex(
          (t) => t.id === action.payload.taskId
        );
        if (index !== -1) tasksForCurrentTodolist.splice(index, 1);
      }
    ),
    addTask: create.reducer<{ task: DomainTask }>((state, action) => {
      const newTask = action.payload.task;
      state[action.payload.task.todoListId].unshift(newTask);
    }),
    updateTask: create.reducer<{
      taskId: string;
      todolistId: string;
      domainModel: UpdateTaskDomainModel;
    }>((state, action) => {
      const tasks = state[action.payload.todolistId];
      const index = tasks.findIndex((t) => t.id === action.payload.taskId);
      if (index !== -1) {
        tasks[index] = { ...tasks[index], ...action.payload.domainModel };
      }
    }),
    clearData: create.reducer(() => {
      return {};
    }),
  }),
  extraReducers: (builder) => {
    builder
      .addCase(addTodolist, (state, action) => {
        state[action.payload.todolist.id] = [];
      })
      .addCase(removeTodolist, (state, action) => {
        delete state[action.payload.id];
      });
  },
});

export const tasksReducer = tasksSlice.reducer;
export const { addTask, removeTask, updateTask, setTasks, clearData } =
  tasksSlice.actions;

//Thunks

export const fetchTasksTC = (todolistId: string) => (dispatch: AppDispatch) => {
  dispatch(setAppStatus({ status: "loading" }));
  taskApi
    .getTasks(todolistId)
    .then((res) => {
      dispatch(setAppStatus({ status: "succeeded" }));
      dispatch(setTasks({ tasks: res.data.items, todolistId }));
    })
    .catch((err) => {
      handleHttpErrors(err, dispatch);
    });
};

export const removeTaskTC =
  (payload: { todolistId: string; taskId: string }) =>
  (dispatch: AppDispatch) => {
    dispatch(setAppStatus({ status: "loading" }));
    taskApi
      .removeTask(payload)
      .then((res) => {
        if (res.data.resultCode === ResultCode.Success) {
          dispatch(setAppStatus({ status: "succeeded" }));
          dispatch(removeTask(payload));
        } else {
          handleAppErrors(dispatch, res.data);
        }
      })
      .catch((err) => handleHttpErrors(err, dispatch));
  };

export const addTaskTC =
  (payload: { todolistId: string; title: string }) =>
  (dispatch: AppDispatch) => {
    dispatch(setAppStatus({ status: "loading" }));
    taskApi
      .createTask(payload)
      .then((res) => {
        if (res.data.resultCode === ResultCode.Success) {
          dispatch(setAppStatus({ status: "succeeded" }));
          dispatch(addTask({ task: res.data.data.item }));
        } else {
          handleAppErrors(dispatch, res.data);
        }
      })
      .catch((err) => {
        handleHttpErrors(err, dispatch);
      });
  };

export const updateTaskTC =
  (payload: {
    taskId: string;
    todolistId: string;
    domainModel: UpdateTaskDomainModel;
  }) =>
  (dispatch: AppDispatch, getState: () => RootState) => {
    dispatch(setAppStatus({ status: "loading" }));
    const state = getState();
    const tasks = state.tasks;
    const tasksForTodolist = tasks[payload.todolistId];
    const task = tasksForTodolist.find(
      (el: { id: string }) => el.id === payload.taskId
    );
    if (!task) {
      console.warn("task not found");
      return;
    }
    const model: UpdateTaskDomainModel = {
      title: task.title,
      status: task.status,
      deadline: task.deadline,
      description: task.description,
      priority: task.priority,
      startDate: task.startDate,
      ...payload.domainModel,
    };

    taskApi
      .updateTask({
        todolistId: task.todoListId,
        taskId: task.id,
        model: model,
      })
      .then((res) => {
        if (res.data.resultCode === ResultCode.Success) {
          dispatch(setAppStatus({ status: "succeeded" }));
          dispatch(updateTask(payload));
        } else {
          handleAppErrors(dispatch, res.data);
        }
      })
      .catch((err) => {
        handleHttpErrors(err, dispatch);
      });
  };
